<template>
    <div class="stationDetial">
        <hheader :header="hheader">
            <i class="iconfont icon-ren" slot="hleft"></i>
        </hheader>
        <section class="container has-header has-gray">
            <div class="top">
                <img src="../../static/img/111.png" alt="">
                <div class="top-footer" @click="show=true">
                    <i class="iconfont icon-pen"></i>
                </div>
            </div>
            <ul class="stationDetial-ul">
                <li>
                    设备名称:{{info.device_name}}
                </li>
            </ul>
            <hr>
            <ul class="stationDetial-ul">
                <li>
                    <span class="li-title">设备类型:</span>{{info.device_type|deviceType}}</li>
                <li>
                    <span class="li-title">报警级别:</span>
                    <span class="badge">{{info.degree|degree}}</span>
                </li>
                <li>
                    <span class="li-title">报警信息:</span>{{info.reason}}</li>
                <li>
                    <span class="li-title">报警来源:</span>{{info.pointion}}</li>
                <li>
                    <span class="li-title">产生次数:</span>
                    <span class="txtRed">{{info.times}}</span>
                </li>
                <li>
                    <span class="li-title">报警时间:</span>{{info.work_time}}</li>
                <li>
                    <span class="li-title">最后时间:</span>{{info.final_time}}</li>
                <li>
                    <span class="li-title">状态:</span>
                    <span class="badge">{{info.deal_status|dealStatus}}</span>
                </li>
            </ul>
        </section>
        <!-- 弹出 -->
        <van-popup v-model="show">
            <div class="h-modal">
                <h4>巡检处理</h4>
                <textarea placeholder="编写巡检信息" v-model="confInfo"></textarea>
                <div>
                    <van-button type="primary" @click.native="show=false" size="mini">取消</van-button>
                    <van-button type="primary" @click.native="Applay()" size="mini">确定</van-button>
                </div>
            </div>
        </van-popup>
    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            hheader: {
                title: "基坑监测项目",
                back: "true"
            },
            show:false,
            workId:'',
            confInfo:"",
            info:{}
        };
    },created(){
        this.workId=HGetHashUrl("workId");
        this.init();
    },methods:{
        //获取数据
        init(){
            let self=this;
            self.$.Get("/project/alarmDetail",{workId:self.workId},re=>{
                self.info=re.info[0];
            })
        },
        //提交
        Applay(){
           let self=this;
           if(!self.confInfo){
             Toast("请输入信息");
           }
           self.$.Post("/project/changeDealStatus",{workId:self.workId,confInfo:self.confInfo},re=>{
              self.show=false;
              self.init();
           })
        }
    }
};
</script>

<style lang="scss" rel="stylesheet/scss">
@import "../assets/hcsy.scss";
.stationDetial {
    .container {
        background-color: white;
    }

    .top {
        height: rem(563);
        position: relative;
        img {
            width: 100%;
            height: 100%;
        }
        .top-footer {
            position: absolute;
            bottom: rem(-81);
            right: rem(90);
            height: rem(162);
            width: rem(162);
            background-color: #28c189;
            color: white;
            border-radius: 50%;
            text-align: center;
            line-height: rem(122);
            i {
                font-size: rem(60);
            }
        }
    }
    .stationDetial-ul {
        font-size: rem(46);
        margin: rem(82) rem(30) rem(60);
        li {
            position: relative;
            padding: rem(23) rem(10);
        }
        .li-title {
            display: inline-block;
            width: rem(210);
            text-align: right;
            margin-right: rem(20);
        }
        .badge {
            background-color: #f85252;
            color: white;
            padding: rem(10) rem(40);
            border-radius: rem(30);
        }
    }
    hr {
        margin: 0 rem(30);
    }
}
.h-modal{
    font-size: rem(40);
    width:rem(1105);
    height: rem(691);
    h4{
        background-color: #28c189;
        line-height: rem(159);
        height: rem(159);
        text-align: center;
        color: white;
        font-size: rem(60);
    }
    textarea{
        width:rem(1042);
        height: rem(321);
        border:1px solid $bordercolor;
        margin: rem(40) rem(30); 
        resize: none;
        padding:rem(10);
    }
    >div{
        .van-button{
            margin-left: rem(140);
            width: rem(272);
            height: rem(87);
            border-radius: rem(40);
            background-color: #bbbbbb;
            border:0;
        }
        .van-button:nth-child(2){
            margin-right: rem(140);
            float: right;
            background-color: #28c189;
        }
    }
}
</style>



